body {
    font-family: 'Poppins', sans-serif;
    background-color: #f8f9fa;
    color: #333;
    margin: 0;
    padding: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 100vh;
  }
  
  .app-container {
    background: white;
    padding: 20px;
    border-radius: 8px;
    box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.1);
    width: 400px;
    text-align: center;
  }
  
  .app-title {
    color: #4caf50;
    font-size: 24px;
    margin-bottom: 10px;
  }
  
  .task-form input {
    padding: 10px;
    width: 70%;
    border: 1px solid #ddd;
    border-radius: 5px;
  }
  
  .task-form button {
    background-color: #e92102;
    color: white;
    border: none;
    padding: 10px;
    cursor: pointer;
    border-radius: 5px;
    margin-left: 5px;
  }
  
  .task-list li {
    background-color: #f4f4f4;
    padding: 10px;
    display: flex;
    justify-content: space-between;
    border-radius: 5px;
    margin: 5px 0;
  }
  
  .task-list button {
    background-color: #f44336;
    color: white;
    border: none;
    cursor: pointer;
    border-radius: 5px;
    padding: 5px;
  }
  
  .task-list li.completed span {
    text-decoration: line-through;
    color: gray;
  }
  
  
  